import "./settings.css";
import { IoCamera } from "react-icons/io5";
import { useContext, useState } from "react";
import { Context } from "../../../context/Context";
import axios from "../../../axios";

export default function Settings() {
	const { user, dispatch } = useContext(Context);
	const [username, setUsername] = useState(user.username);
	const [email, setEmail] = useState(user.email);
	const [password, setPassword] = useState("");
	const [file, setFile] = useState(null);
	const PF = "https://puce-glamorous-cocoon.cyclic.app/images/";
	const [showConfirm, setShowConfirm] = useState(false);

	const handleUpdate = async (e) => {
		e.preventDefault();
		dispatch({ type: "UPDATE_START" });
		console.log(username);
		const updatedUser = {
			userId: user._id,
			username,
			email,
			password,
		};

		if (file) {
			const data = new FormData();
			const fileName = Date.now() + file.name;
			data.append("name", fileName);
			data.append("file", file);
			updatedUser.profilePic = fileName;
			try {
				await axios.post("/upload", data);
			} catch (error) {}
		}
		try {
			const res = await axios.put("/users/" + user._id, updatedUser);
			console.log(res);
			dispatch({ type: "UPDATE_SUCCESS", payload: res.data });
			// window.location.reload();
		} catch (error) {
			dispatch({ type: "UPDATE_FAILURE" });
		}
	};

	const handleDelete = async (e) => {
		e.preventDefault();
		try {
			const res = await axios.delete("/users/" + user._id);
			console.log(res);
			dispatch({ type: "DELETE_SUCCESS" });
			window.location.replace("/");
		} catch (error) {
			console.log(error);
			dispatch({ type: "DELETE_FAILURE" });
		}
	};

	return (
		<div className="settings">
			<div className="settingsWrapper">
				<div className="settingsTitle">
					<span className="updateTitle">Update Your Profile</span>
					<button
						type="button"
						class="btn btn-outline-danger deleteTitle"
						onClick={() => setShowConfirm(true)}
					>
						Delete Profile
					</button>

					{showConfirm && (
						<>
							<div className="overlay"></div>

							<div className="popUp">
								<p>
									Your account and posts uploaded by you will be deleted
									permanently
								</p>
								<button
									type="button"
									class="btn btn-outline-danger"
									onClick={(e) => handleDelete(e)}
								>
									Delete
								</button>
								<button
									type="button"
									class="btn btn-outline-secondary"
									onClick={() => setShowConfirm(false)}
								>
									Cancel
								</button>
							</div>
						</>
					)}
				</div>
				<form className="settingsForm" onSubmit={(e) => handleUpdate(e)}>
					<div className="settingsPP">
						{file ? (
							<img
								src={window.webkitURL.createObjectURL(file)}
								alt="profilePic"
							></img>
						) : (
							<img
								src={
									user.profilePic
										? PF + user.profilePic
										: "data:image/png;base64,"
								}
								className="profilePic"
								alt="profilePic"
							></img>
						)}
						<div class="overlayImg">
							<label htmlFor="fileInput">
								<IoCamera className="overlayIcon" />
							</label>
						</div>
						<input
							type="file"
							id="fileInput"
							style={{ display: "none" }}
							onChange={(e) => {
								setFile(e.target.files[0]);
							}}
						/>
					</div>
					<div className="userDetails">
						<label>Username</label>
						<input
							type="text"
							placeholder={user.username}
							value={username}
							autoFocus
							onChange={(e) => setUsername(e.target.value)}
						></input>
						<label>Email</label>
						<input
							type="email"
							placeholder={user.email}
							value={email}
							onChange={(e) => setEmail(e.target.value)}
						></input>
						<label>Password</label>
						<input
							className="passInput"
							type="password"
							placeholder="enter a password"
							onChange={(e) => setPassword(e.target.value)}
							required
						></input>
					</div>
					<button className="updateBtn btn btn-outline-success" type="submit">
						Update
					</button>
				</form>
			</div>
		</div>
	);
}
